import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm } from "redux-form";
import axios from "axios";
import AccordCash from "./AccordCash";
import AccordTransfer from "./AccordTransfer";
import AccordCiok from "./AccordCiok";
import AccordRongsok from "./AccordRongsok";
import { setCashTemp, setTransferTemp } from "../../actions/customerAction";
import Swal from "sweetalert2";
import AccordCashMurni from "./AccordCashMurni";
import AccordTransferMurni from "./AccordTransferMurni";
import { createNumberMask } from "redux-form-input-masks";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const server = process.env.REACT_APP_BACKEND_URL;
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class TitipanRupiah extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
    };
  }

  componentDidMount() {
    // Set date
    const date = new Date();
    var formatedDateNow = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    this.props.change("tanggal", formatedDateNow);
    // set Supplier
    axios
      .get(server + "suppliers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch((err) => console.log(err));
    this.setCiok();
    this.setRongsok();
  }
  onChange() {
    this.props.change("cashNetto", this.props.cashNetto);
    this.props.change("transferNetto", this.props.transferNetto);
  }
  setCiok() {
    this.props.change("ciokBrutto", this.props.totalBrutoCiok);
    this.props.change("ciokNetto", this.props.totalCiok);
  }
  setRongsok() {
    this.props.change("rongsokBruto", this.props.totalBrutoRongsok);
    this.props.change("rongsokNetto", this.props.totalRongsok);
    this.props.change("noTitip", this.props.noFaktur);
  }

  handleSubmitCash(data) {
    var isi = {
      tunaiRp: data.cash,
      tunaiKurs: data.harga,
      tunaiTipe: "BON GR",
      tunaiNetto: data.cashNetto,
    };
    var cashTemp = JSON.parse(localStorage.getItem("cashTemp")) || [];
    cashTemp.push(isi);
    localStorage.setItem("cashTemp", JSON.stringify(cashTemp));
    var alatBayarTunai =
      JSON.parse(localStorage.getItem("alatBayarTunai")) || [];
    alatBayarTunai.push(isi);
    localStorage.setItem("alatBayarTunai", JSON.stringify(alatBayarTunai));
    this.props.dispatch(setCashTemp());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.change("totalBayar", this.props.totalBayar);
  }
  handleSubmitCashRp(data) {
    var isi = {
      tunaiRp: data.cash,
      tunaiKurs: data.harga,
      tunaiTipe: "BON GR",
      tunaiNetto: data.cashNetto,
    };
    var cashTemp = JSON.parse(localStorage.getItem("cashTempRp")) || [];
    cashTemp.push(isi);
    localStorage.setItem("cashTempRp", JSON.stringify(cashTemp));
    var alatBayarTunai =
      JSON.parse(localStorage.getItem("alatBayarTunaiRp")) || [];
    alatBayarTunai.push(isi);
    localStorage.setItem("alatBayarTunaiRp", JSON.stringify(alatBayarTunai));
    this.props.dispatch(setCashTemp());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.change("totalBayarCash", this.props.totalBayarRp);
  }
  handleSubmitTransfer(data) {
    var isi = {
      transferKurs: data.harga,
      transferNetto: data.transferNetto,
      transferRp: data.transfer,
      transferTipe: data.kriteria,
      accFrom: data.supplier,
      accTo: data.tujuan,
      transferTanggalJanji: data.tanggal,
    };
    var transferTemp = JSON.parse(localStorage.getItem("transferTemp")) || [];
    transferTemp.push(isi);
    localStorage.setItem("transferTemp", JSON.stringify(transferTemp));
    var alatBayarTrf = JSON.parse(localStorage.getItem("alatBayarTrf")) || [];
    alatBayarTrf.push(isi);
    localStorage.setItem("alatBayarTrf", JSON.stringify(alatBayarTrf));
    this.props.dispatch(setTransferTemp());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });

    this.props.change("totalBayar", this.props.totalBayar);
  }
  handleSubmitTransferRp(data) {
    var isi = {
      transferKurs: data.harga,
      transferNetto: data.transferNetto,
      transferRp: data.transfer,
      transferTipe: data.kriteria,
      accFrom: data.supplier,
      accTo: data.tujuan,
      transferTanggalJanji: data.tanggal,
    };
    var transferTemp = JSON.parse(localStorage.getItem("transferTempRp")) || [];
    transferTemp.push(isi);
    localStorage.setItem("transferTempRp", JSON.stringify(transferTemp));
    var alatBayarTrf = JSON.parse(localStorage.getItem("alatBayarTrfRp")) || [];
    alatBayarTrf.push(isi);
    localStorage.setItem("alatBayarTrfRp", JSON.stringify(alatBayarTrf));
    this.props.dispatch(setTransferTemp());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.change("totalBayarCash", this.props.totalBayarRp);
  }

  setTotal() {
    this.props.change("totalBayar", this.props.totalBayar);
    this.props.change("totalBayarCash", this.props.totalBayarRp);
  }
  render() {
    return (
      <>
        <div className="row">
          {this.props.from === "rupiah" ? (
            <>
              <AccordCash onSubmit={(data) => this.handleSubmitCash(data)} />
              <AccordTransfer
                onSubmit={(data) => this.handleSubmitTransfer(data)}
              />
              <AccordCiok
                totalCiok={this.props.totalCiok}
                totalBrutoCiok={this.props.totalBrutoCiok}
              />
              <AccordRongsok
                totalRongsok={this.props.totalRongsok}
                totalBrutoRongsok={this.props.totalBrutoRongsok}
              />
            </>
          ) : (
            <>
              <AccordCashMurni
                onSubmit={(data) => this.handleSubmitCashRp(data)}
              />
              <AccordTransferMurni
                onSubmit={(data) => this.handleSubmitTransferRp(data)}
              />
            </>
          )}
        </div>
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-lg-4 col-md-4">
              <Field
                name="noTitip"
                component={renderField}
                type="text"
                label="No Titip"
                readOnly={true}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <Field
                name="tanggal"
                label="Tanggal"
                type="date"
                component={renderField}
              />
            </div>
            <div className="col-md-4 col-lg-4">
              <label htmlFor="">Supplier</label>
              <Field
                name="supplier"
                className="form-control"
                component="select"
              >
                <option value="DEFAULT" key="DEFAULT">
                  silahkan pilih
                </option>
                {this.state.listSupplier.map((list) => (
                  <option
                    value={list.kodeSupplier + "|" + list.namaSupplier}
                    key={list.kodeSupplier}
                  >
                    {list.namaSupplier}
                  </option>
                ))}
              </Field>
            </div>
            <div className="col-md-6 col-lg-6">
              <label htmlFor="">Kriteria</label>
              <Field
                name="kriteria"
                className="form-control"
                component="select"
              >
                <option value="DEFAULT" key="DEFAULT">
                  SILAHKAN PILIH
                </option>
                {this.props.from === "rupiah" ? (
                  <>
                    <option value="tambahTitipanGr" key="tambahTitipan">
                      TAMBAH TITIPAN
                    </option>
                    <option value="tarikTitipanGr" key="tarikTitipan">
                      TARIK TITIPAN
                    </option>
                  </>
                ) : (
                  <>
                    <option value="tambahTitipanRp" key="tambahTitipan">
                      TAMBAH TITIPAN
                    </option>
                    <option value="tarikTitipanRp" key="tarikTitipan">
                      TARIK TITIPAN
                    </option>
                  </>
                )}
              </Field>
            </div>
            <div className="col-lg-6"></div>
            <div className="col-lg-12">
              <div className="card">
                <div className="row p-3">
                  <div
                    className={
                      this.props.from === "rupiah"
                        ? "col-lg-4 col-md-4"
                        : "col-lg-4 col-md-4 d-none"
                    }
                  >
                    <div className="form-group">
                      <Field
                        name="totalBayar"
                        label="Total Gram"
                        type="number"
                        component={renderField}
                        onBlur={this.setTotal()}
                      />
                    </div>
                  </div>
                  <div
                    className={
                      this.props.from !== "rupiah"
                        ? "col-lg-4 col-md-4"
                        : "col-lg-4 col-md-4 d-none"
                    }
                  >
                    <div className="form-group">
                      <label htmlFor="">Total Bayar Rp</label>
                      <Field
                        name="totalBayarCash"
                        label="Total Rp"
                        type="telp"
                        component="input"
                        className="form-control"
                        onBlur={this.setTotal()}
                        {...currencyMask}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-lg-12 col-md-12">
              <div className="text-right">
                <button className="btn btn-primary" type="submit">
                  Simpan
                </button>
              </div>
            </div>
          </div>
        </form>
      </>
    );
  }
}

TitipanRupiah = reduxForm({
  form: "TitipanRupiah",
  enableReinitialize: true,
})(TitipanRupiah);
const selector = formValueSelector("TitipanRupiah"); // <-- same as form name
export default connect((state) => {
  const { harga, cash, transfer } = selector(
    state,
    "harga",
    "cash",
    "transfer"
  );
  var bayarTrf = JSON.parse(localStorage.getItem("alatBayarTrf"))
    ? JSON.parse(localStorage.getItem("alatBayarTrf"))
    : 0;
  var bayarTunai = JSON.parse(localStorage.getItem("alatBayarTunai"))
    ? JSON.parse(localStorage.getItem("alatBayarTunai"))
    : 0;
  var bayarTrfRp = JSON.parse(localStorage.getItem("alatBayarTrfRp"))
    ? JSON.parse(localStorage.getItem("alatBayarTrfRp"))
    : 0;
  var bayarTunaiRp = JSON.parse(localStorage.getItem("alatBayarTunaiRp"))
    ? JSON.parse(localStorage.getItem("alatBayarTunaiRp"))
    : 0;
  var totalCiok = JSON.parse(localStorage.getItem("totalCiok"))
    ? JSON.parse(localStorage.getItem("totalCiok"))
    : 0;
  var totalRosok = JSON.parse(localStorage.getItem("totalRongsok"))
    ? JSON.parse(localStorage.getItem("totalRongsok"))
    : 0;
  var tunai = bayarTunai[0] === undefined ? 0 : bayarTunai[0].tunaiNetto;
  var transferr = bayarTrf[0] === undefined ? 0 : bayarTrf[0].transferNetto;
  var tunaiRp = bayarTunaiRp[0] === undefined ? 0 : bayarTunaiRp[0].tunaiNetto;
  var transferrRp =
    bayarTrfRp[0] === undefined ? 0 : bayarTrfRp[0].transferNetto;
  return {
    cashNetto: (parseFloat(cash) / parseFloat(harga)).toFixed(3),
    transferNetto: (parseFloat(transfer) / parseFloat(harga)).toFixed(3),
    totalBayar: (
      parseFloat(tunai) +
      parseFloat(transferr) +
      parseFloat(totalCiok) +
      parseFloat(totalRosok)
    ).toFixed(3),
    totalBayarRp: (parseFloat(tunaiRp) + parseFloat(transferrRp)).toFixed(3),
  };
})(TitipanRupiah);
